@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap');
@import url('https://fonts.googleapis.com/css?family=Oleo+Script+Swash+Caps:400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Federo&display=swap');
@import url('https://fonts.googleapis.com/css?family=Libre+Caslon+Text&display=swap');
body{
    padding: 0;
    margin: 0;
}
*{
    box-sizing: border-box;
}
%font-a{
    font:16px/21px Roboto,sans-serif;
    text-decoration: none;
    color: #454851;
    font-weight: 500;  
    text-transform: uppercase;
    letter-spacing: 0.4px;
}
%font-h3{
    font:24px Federo,sans-serif;
        color: #171d1c;  
        font-weight: bold;
}
%font-p{
    font:15.3px/22px Roboto,sans-serif;
    color: #454851;
    font-weight: 300;
}


.wrapper{
    max-width: 1406px;
	min-width: 320px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	padding:0 20px;
    margin: auto;
    
}
header{
    background: white;
    width: 100%;
    margin-top:25px;
}
#burg a{
    display: block;
    width: 47px;
    margin-right: 2%;
}
#burg{
    .logo-mob{
        font-family: Oleo Script Swash Caps, cursive;
        font-size: 24px;
        font-weight: 400;
        text-decoration: none;
        width: 161px;
        color: white;
    }
}
#burg{
    width: 100%;
    background: #232323;
    padding: 7.5px 0 7.5px 2%;
    display: flex;
    justify-content: space-between;
    background: #4485c1ed;
    a:hover{
        border: 1px solid white;  
    }
span{
    display: flex;
    margin: 8px;
    width: 30px;
    height: 1px;
    background: white;
    z-index: 999;
}
}
#desktop{
    display: none;
}
#mobile{
    text-align: center;
}
.mobile-tab{
    display: flex;
    width: 100%;
    background: #4485c1ed;
    justify-content: center;
}
.mobile-tab a{
    font:16px/21px Roboto,sans-serif;
    text-decoration: none;
    color: #eeeff3;
    font-weight: 500;  
    text-transform: uppercase;
    letter-spacing: 0.4px;
    transition: 0.3s linear;
     a:hover{
        background: #06948070;
    }
}
.mobile-tab li{
    list-style-type: none;
    margin: 5% 5%;
}
.mobile-tab ul{
    padding:5% 0;
    width: 170px;
}

@media (min-width:768px){
    .mobile-tab,#burg,#mobile{
        display: none;
    }
    header{
        margin:25px 0;
    }
    .bar{
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
    .mobile-bar{
        display: none;
        
    }
    #desktop{
        display: flex;
        justify-content: center;
        width: 100%;
    
    
    ul{
        display:flex;
        width: 100%;
        list-style-type: none;
        padding: 0;
        margin: 0;
        justify-content: space-around;
        li{
             margin-left:auto;
             position: relative;
             .list{
                color: #171d1c;
                font-family: Oleo Script Swash Caps, cursive;
                font-size: 36px;
                font-weight: 400;
                line-height: 21px;
               }
             a{
                font:16px/21px Roboto,sans-serif;
                text-decoration: none;
                color: #454851;
                font-weight: 500;  
                text-transform: uppercase;
                letter-spacing: 0.4px;
                padding:10px;
                 transition: 0.2s linear;
                 border-radius: 8px;
                 
            }
            & a:hover{
                 background: #06948070;
                }
        }
            & li:last-child{
                margin-right: auto;
                }
            & li:nth-child(n+2):after{
                content: "";
                background: url(../images/romb.png);
                position: absolute;
                width: 7px;
                height: 7px;
                left: -40px;
                top: 25.5%;
            }

        }
    }
}
@media(max-width:1200px){
    #desktop{
        ul{
            li:nth-child(n+2):after{
                left:-20px;
            }
            
        }
    }
}
@media(max-width:925px){
    #desktop{
        ul{
            li:nth-child(n+2):after{
                left:-10px;
            }
            
        }
    }
}
@media(max-width: 910px){
    #desktop{
        ul{
            flex-direction: column;
            align-items: center;
            li:last-child{
                margin-right: 0;
                margin-top:20px;
            }
            li{
                margin-left:0;
                margin:15px 0;
            }
            & li:nth-child(n+2):after{
                background: none;
            }
                
        }
            
    }
}
main{
    width: 100%;
    background: #ecf0f1;
    
}
