.form{ 
    width: 100%;
    margin: 50px 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    form{
        background: #ffffff;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
