    }
    & input:hover{
        color: #454851;
        border-color: #454851;
    }
     & select:hover{
      color: #454851;
      border-color:#454851;
  }
    & select:last-child{
        margin-left:19px;
    }
    & .sel-name-one::after{
        content: url(../images/dropdown_copy_2.png);
        position: absolute;
        left: 22%;
        top: 44%;
        background: white;
    }
    & .sel-name-two::before{
        content: url(../images/dropdown_copy_2.png);
        position: absolute;
        left: 89%;
        top: 44%;
        background: white; 
        z-index: 1;
    }
    & .sel-name-two::before{
        content: url(../images/dropdown_copy_2.png);
        position: absolute;
        left: 89%;
        top: 44%;
        background: white; 
        z-index: 1;
    } 
    & .calend-first::before{
        content: url(../images/dropdown_copy_2.png);
        position: absolute;
        left: 44%;
        top: 44%;
        background: white; 
        
    }
    & .calend-second::before{
        content: url(../images/dropdown_copy_2.png);
        position: absolute;
        left: 65.5%;
        top: 44%;
        background: white; 
        
    }

    
    .btn-kal{
        width: 41px;
        height: 40px;
        background-color: #f31131;   
        border-radius: 26px;
        padding: 3px;
        border: 0;
        margin-left: 30px;
        color: #ffffff;
        font-size: 30px;
        font-weight: 400;
    }
    
