.owl-carousel.owl-carousel .owl-item img {
    width: auto;
 }
 .owl-theme.owl-theme .owl-dots .owl-dot span {
 
    background: rgb(214, 203, 203);
    border-radius: 0px;
    transform: rotate(45deg);
    border:0;
}
.owl-theme.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
    background: black;
}
.owl-theme .owl-dots .owl-dot {
    outline: none;
}
.no-js .owl-carousel, .owl-carousel.owl-loaded.owl-carousel.owl-loaded {
    display: flex;
    flex-direction: column-reverse;
}
.owl-theme.owl-theme .owl-nav.disabled+.owl-dots {
    margin-bottom: 20px;
    margin-top:0;
    display: block;
    
}

.owl-theme.owl-theme .owl-dots .owl-dot span {
    width: 6px;
    height: 6px;
    transition: 0.1s padding linear;
}
.owl-theme.owl-theme .owl-dots .owl-dot:hover span{
    background: none;
}
.owl-theme.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:focus span {
    background: black;
    width:9px;
    height: 9px;
    
}
.owl-theme .owl-dots .owl-dot {
    display: inline-block;
    zoom: 1;
    vertical-align: middle;
}
.owl-theme.owl-theme .owl-dots .owl-dot span {
    background: #ecf0f1;
    border-radius: 0px;
    transform: rotate(45deg);
    border: 1px solid;
}

